@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.main{display:flex;flex-direction:row;width:100vw;height:100vh;overflow:hidden}

:root{--bullet-size-active: 1em;--bullet-size-idle: 0.8em}.bullet{border-radius:50%;margin:0.2em;transition:0.7s}.idle{height:0.8em;height:var(--bullet-size-idle);width:0.8em;width:var(--bullet-size-idle);background-color:rgba(0,0,0,0.3)}.active{height:1em;height:var(--bullet-size-active);width:1em;width:var(--bullet-size-active);background-color:#149ddd}.tooltip{position:relative;display:inline-block}.tooltip .tooltiptext{visibility:hidden;width:120px;text-align:center;border-radius:6px;padding:5px 0;position:absolute;z-index:1}.tooltip .tooltiptext{width:120px;top:100%;left:50%;margin-left:-60px}.tooltip:hover .tooltiptext{visibility:visible}

.bullet-bar{position:fixed;display:flex;width:100%;min-height:2em;justify-content:center;align-items:center;padding-top:0.5em}

.container{display:flex;width:100vw;min-height:-webkit-min-content;min-height:min-content;overflow:auto}

.about-div{margin:5em;-webkit-animation:fadeIn 1s ease-out;animation:fadeIn 1s ease-out}.about-description{display:flex}.about-details-container{display:flex;flex-direction:column}.about-details-lists{display:flex}.about-details-list{font-size:0.1em}.about-details-title{margin:0;margin-bottom:1em;font-size:1.5em;color:var(--dark-text-color)}.about-details-list-container{display:flex;flex-direction:row}.about-details-list{margin-right:1em}.about-details-list li{list-style-image:url("https://api.iconify.design/bi/chevron-right.svg");margin-bottom:1em}.about-details-list li div *{font-size:1.2em}

.section-title{font-size:2em;font-weight:bold;position:relative;color:#173b6c}.section-title::after{margin-top:0.3em;content:'';display:block;width:2em;height:3px;background:#149ddd;bottom:0;left:0}

.contact-container{margin-bottom:2em}.contact-form{width:100%;padding:30px;background:#fff;display:flex;flex-direction:column;align-items:flex-end;color:--var(--dark-text-color)}.contact-row{display:flex;flex-direction:row;width:100%}.contact-form .contact-info{display:flex;flex-direction:column;margin-bottom:15px;width:100%;padding:1em}.contact-form label{padding-bottom:8px;font-size:1.2em;margin-bottom:0.5em;color:--var(--dark-text-color)}.contact-form input,.contact-form textarea{font-size:14px}.contact-form input{height:44px}.contact-form textarea{padding:10px 15px;height:5em}.contact-form button[type='submit']{background:#173b6c;border:0;padding:10px 24px;color:#fff;transition:0.4s;border-radius:4px;margin-right:1em}.contact-form button[type='submit']:hover{background:#37b3ed}.contact-info{color:#173b6c}

.contact-div{margin:5em;-webkit-animation:fadeIn 1s ease-out;animation:fadeIn 1s ease-out}

.resume-item{padding:0 0 1em 1em;border-left:2px solid #1f5297;position:relative;margin-bottom:-1.3em}.resume-item:last-child{padding-bottom:0;margin-bottom:1em}.resume-item:before{content:'';position:absolute;width:16px;height:16px;border-radius:50px;left:-0.55em;top:0;background:#fff;border:2px solid #1f5297}.resume-item h4{margin-top:0;line-height:1em;font-size:1em;font-weight:600;text-transform:uppercase;color:#050d18;margin-bottom:1em}.resume-item h5{margin-top:0;margin-bottom:0;font-size:0.8em;background:#e4edf9;padding:0.5em 1em;display:inline-block;font-weight:600}

.resume-div{width:100%;-webkit-animation:fadeIn 1s ease-out;animation:fadeIn 1s ease-out}.resume-title{font-size:1em;font-weight:700;color:#050d18}.resume-title-container{margin-left:5em;margin-top:5em}.resume-container{display:flex;flex-wrap:wrap;justify-content:space-evenly}.resume-container div{display:flex;flex-direction:column;max-width:700px;width:100%;padding:1em}

.portfolio-div{margin:5em;-webkit-animation:fadeIn 1s ease-out;animation:fadeIn 1s ease-out}.portfolio-link{color:--var(--menu-text-highlight);text-decoration:none}

.home{display:flex;background-image:url(../../static/media/home-bg.1b16acbc.png);flex-direction:column;align-items:flex-end;justify-content:center;padding-right:30vh;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover;height:100vh;width:100vw;-webkit-animation:fadeIn 1s ease-out;animation:fadeIn 1s ease-out}.home-name{color:var(--light-text-color);font-size:2.5em;font-weight:bolder}.home-label{color:var(--light-text-color)}.home-label-text,.typed-cursor{font-size:1.5em}

.arrow-container{cursor:pointer;height:100vh;display:flex;align-items:center;opacity:0.5;transition:1s}.arrow-container:hover{opacity:1}.arrow-container svg{font-size:3em;color:rgba(0,0,0,0.4)}.arrow-container svg:hover{color:#149ddd}

.arrows-container{position:fixed;display:flex;justify-content:space-between;align-items:center;height:100vh;width:100vw}

:root{--dark-background: #040b14;--light-background: #ffffff;--light-blue-background: #f5f8fd;--light-blue-highlight: #149ddd;--light-text-color: #fff;--dark-text-color: #173b6c;--menu-background-hover: rgba(255, 255, 255, 0.1);--menu-text-color: rgba(205, 205, 212, 0.8);--menu-text-highlight: #149ddd;--social-links: #212431;--social-links-highlight: #149ddd;--scroll-bar-track-color: rgba(0, 0, 0, 0.1);--scroll-bar-color: rgba(0, 0, 0, 0.4)}*{font-family:'Roboto', sans-serif;font-size:20px}body{margin:0}@-webkit-keyframes fadeIn{0%{display:none;opacity:0}1%{display:block;opacity:0}100%{display:block;opacity:1}}@keyframes fadeIn{0%{display:none;opacity:0}1%{display:block;opacity:0}100%{display:block;opacity:1}}

